//FLEXBOX

.content-col {
  display: flex;
  flex-direction: column;
  // justify-content: space-between;
}

.content-row {
  display: flex;
  flex-direction: row;
  // justify-content: space-between;
}

.center {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  &:hover {
    font-weight: bold;
  }
}

.col {
  width: 33.3%;
}

.col-1-3 {
  width: 33.3%;
  flex: 1 0;
}

.col-2-3 {
  width: 66.6%;
  flex: 2 0;
}

.col-1-4 {
  width: 25%;
  flex: 1 0;
}

.col-3-4 {
  width: 75%;
  flex: 3 0;
}

.col-1-2 {
  width: 50%;
  flex: 1 0;
}

.col-1-2ish {
  width: 70%;
  flex: 2 0;
}

.col-2-2 {
  max-width: 600px;
  width: 30%;
  flex: 1 0;
}
